.profile{position:relative}.profile__background-img{position:absolute;z-index:-100;max-width:calc(100vw - 80px);min-height:40vh;object-fit:cover;-webkit-mask-image:linear-gradient(to bottom, rgba(0,0,0,0.9), rgba(0,0,0,0));mask-image:linear-gradient(to bottom, rgba(0,0,0,0.9), rgba(0,0,0,0))}.profile__row:first-of-type{padding:11% 1.9% 1.9% 4%}.profile__row:first-of-type .profile__row-wrapper{background-image:linear-gradient(30deg, rgba(42,156,42,0.1), rgba(180,180,180,0.4));box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 10px 80px rgba(0,0,0,0.12);border-radius:10px;padding:1rem 0 1rem 0}.profile__row:last-of-type{padding:0 1.9% 0 4%;display:flex;justify-content:space-evenly;align-items:flex-start;flex-direction:row}

.btn{display:inline-block}.btn>button{outline:none;cursor:pointer;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:inline-block;padding:9px 15px;font-size:smaller;font-weight:700;border:none;border-radius:10px;color:transparent;color:#f8f9fb;background-image:linear-gradient(to bottom right, rgba(255,0,0,0.1), rgba(111,66,193,0.8));transition:all 0.4s ease-in-out}.btn>button:hover{color:#6f42c1}

.my-posts{flex:2 1;padding:1rem 0.5rem 0 0.5rem;height:100%}.my-posts__wrapper{display:flex;justify-content:flex-start;align-items:center;flex-direction:column;background-image:linear-gradient(30deg, rgba(42,156,42,0.1), rgba(180,180,180,0.4));box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12);border-radius:10px;padding:1rem 0.5rem 1rem 0.5rem;margin-bottom:1rem}.my-posts__title{margin-top:0;position:relative;display:inline-block;color:rgba(111,66,193,0.9);height:29px}.my-posts__title::after{position:absolute;content:"";background:rgba(102,16,242,0.9);height:3px;width:50%;bottom:-1vw;right:0;border-radius:20px;transform:translate(-50%, -20%)}textarea{border-radius:0.2rem;width:13rem;height:3rem}.my-posts__posts{margin-top:0.4rem;width:100%}.my-posts-form{width:100%}.my-posts-form__btn{text-align:right;margin-top:0.5rem;margin-right:0.5rem}.my-posts-form__textarea{width:100%;border:none;outline:none;border-radius:0;border-bottom:solid 2px rgba(111,66,193,0.4);background-color:transparent;resize:none}.my-posts-form__textarea::-webkit-input-placeholder{color:rgba(111,66,193,0.9)}.my-posts-form__textarea:-ms-input-placeholder{color:rgba(111,66,193,0.9)}.my-posts-form__textarea::-ms-input-placeholder{color:rgba(111,66,193,0.9)}.my-posts-form__textarea::placeholder{color:rgba(111,66,193,0.9)}

.post{display:flex;justify-content:flex-start;align-items:center;flex-direction:row;margin-top:1rem;padding:0.7rem}.post__img{display:inline-block;object-fit:cover;width:4rem;height:4rem;border-radius:50%;margin-right:1rem}.post__text{color:rgba(111,66,193,0.8)}

.profile-photo-status{display:flex;justify-content:flex-start;align-items:flex-end;flex-direction:row;margin-bottom:0.8%}.profile-photo-status__img-wrapper{position:relative;margin-left:3%}.profile-photo-status__img{width:13vw;height:13vw;object-fit:cover;border:3px solid #f8f9fb;border-radius:10px;box-shadow:0 8px 16px 0 rgba(111,66,193,0.2);transition:all 0.4s ease-in-out}.profile-photo-status__img:hover{box-shadow:0 2.8px 2.2px rgba(111,66,193,0.034),0 6.7px 5.3px rgba(111,66,193,0.048),0 12.5px 10px rgba(111,66,193,0.06),0 22.3px 17.9px rgba(111,66,193,0.072),0 41.8px 33.4px rgba(111,66,193,0.086),0 20px 40px rgba(111,66,193,0.12)}.profile-photo-status__img:hover+.profile-photo-status__visibilety-edit-btn{background-color:rgba(111,66,193,0.7);color:#f8f9fa}.profile-photo-status__visibilety-edit-btn{position:absolute;height:2.1vw;top:86%;left:86%;color:transparent;transform:translate(-50%, -50%);background-color:transparent;border-radius:5px;border:none;outline:none;cursor:pointer;display:flex;justify-content:center;align-items:center;flex-direction:row;transition:all 0.4s ease-in-out}.profile-photo-status__visibilety-edit-btn>svg{font-size:1.5vw;transition:all 0.4s ease-in-out}.profile-photo-status__visibilety-edit-btn:hover{background-color:rgba(111,66,193,0.7);color:#f8f9fa}.profile-photo-status__fullname{font-family:"Quicksand",Verdana,sans-serif;font-size:1.8vw;margin-left:3%}.profile-photo-status__last-name{color:#6f42c1;font-weight:700}.edit-photo{visibility:hidden;margin-left:4%;margin-bottom:0.5%;transition:all 0.4s ease-in-out}.edit-photo .edit-photo__custom-btn{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:inline-block;padding:4px 8px;font-size:smaller;font-weight:700;border-radius:10px;color:transparent;transition:all 0.4s ease-in-out}.edit-photo_show{visibility:visible;visibility:initial}.edit-photo_show .edit-photo__custom-btn{color:rgba(111,66,193,0.8);cursor:pointer;background-image:linear-gradient(to bottom right, rgba(180,180,180,0.3), #f8f9fb)}.edit-photo__label{position:relative}.edit-photo__label>input{visibility:hidden;position:absolute;z-index:-50}

.profile-status{margin-bottom:1.5vw;margin-left:1vw;padding:4px 7px 6px;background-color:#6f42c1;display:inline-block;border-radius:7px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:#f8f9fa;font-family:"Quicksand",Verdana,sans-serif;font-size:small}.profile-status>input{background-color:rgba(111,66,193,0.8);outline:none;border:none;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:#f8f9fa}

.profile-info{padding:1rem 0 0 0.5rem;flex:1 1}.profile-info__wrapper{background-image:linear-gradient(30deg, rgba(255,0,0,0.1), rgba(180,180,180,0.4));box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12);border-radius:10px;padding:1rem 0.5rem 1rem 0.5rem;margin-bottom:1rem;text-align:right}

.person-data__fullname,.person-data__about-me,.person-data__looking-for-job,.person-data__position-description{width:100%;display:flex;justify-content:center;align-items:flex-end;flex-direction:column;padding-right:0.5rem;margin-bottom:0.7rem;border-right:solid 4px rgba(111,66,193,0.7);border-radius:5px}.person-data__title{display:inline-block;margin-bottom:0.3rem;font-size:0.7rem;color:#343a40}.person-data__value{text-align:right;font-size:0.9rem;font-weight:700;color:rgba(111,66,193,0.8)}.person-data__contacts{display:grid;grid-template-columns:repeat(auto-fill, minmax(2rem, 1fr));justify-items:center;align-items:center;font-size:1rem;margin-bottom:1rem}.person-data__contacts>div{display:flex;justify-content:center;align-items:center;flex-direction:row}.person-data__contacts>div>a{display:flex;justify-content:center;align-items:center;flex-direction:row}.person-data__contacts>div>a>svg{color:#343a40;transition:all 0.4s ease-in-out}.person-data__contacts>div>a>svg:hover{color:rgba(111,66,193,0.7);transform:scale(1.2)}

.person-data-form__fullname,.person-data-form__about-me,.person-data-form__looking-for-job,.person-data-form__position-description,.person-data-form__github,.person-data-form__instagram,.person-data-form__linkin,.person-data-form__facebook,.person-data-form__twitter,.person-data-form__youtube,.person-data-form__website{width:100%;display:flex;justify-content:center;align-items:stretch;flex-direction:column;padding-right:0.5rem;margin-bottom:0.7rem;border-right:solid 4px rgba(111,66,193,0.7);border-radius:5px}.person-data-form__github>svg,.person-data-form__instagram>svg,.person-data-form__linkin>svg,.person-data-form__facebook>svg,.person-data-form__twitter>svg,.person-data-form__youtube>svg,.person-data-form__website>svg{align-self:flex-end}.person-data-form__title{display:inline-block;margin-bottom:0.3rem;font-size:0.7rem;color:#343a40}.person-data-form__value input,.person-data-form__value textarea{text-align:right;background-color:transparent;width:100%;border:none;outline:none;border-radius:0;border-bottom:solid 2px rgba(111,66,193,0.4);background-color:transparent;color:#6f42c1;font-size:0.9rem;font-weight:700;font-family:"Quicksand", Verdana, sans-serif;resize:none}.person-data-form__value input::-webkit-input-placeholder, .person-data-form__value textarea::-webkit-input-placeholder{color:rgba(111,66,193,0.9)}.person-data-form__value input:-ms-input-placeholder, .person-data-form__value textarea:-ms-input-placeholder{color:rgba(111,66,193,0.9)}.person-data-form__value input::-ms-input-placeholder, .person-data-form__value textarea::-ms-input-placeholder{color:rgba(111,66,193,0.9)}.person-data-form__value input::placeholder,.person-data-form__value textarea::placeholder{color:rgba(111,66,193,0.9)}.person-data-form__looking-for-job .person-data-form__value{align-self:flex-end;width:1rem}

.profile-following{flex:1 1;padding:1rem 0.5rem 0 0}.profile-following__wrapper{display:flex;justify-content:center;align-items:center;flex-direction:column;background-image:linear-gradient(30deg, rgba(255,0,0,0.1), rgba(180,180,180,0.4));box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12);border-radius:10px;padding:1rem 0.5rem 1rem 0.5rem;margin-bottom:1rem}.profile-following__title{position:relative;display:inline-block;margin-bottom:1rem;font-weight:500;font-size:1.1rem;color:rgba(111,66,193,0.9);height:29px}.profile-following__title::after{position:absolute;content:"";background:rgba(102,16,242,0.9);height:3px;width:50%;bottom:-1vw;right:0;border-radius:20px;transform:translate(-50%, 0)}.profile-following__users{width:100%;display:grid;grid-template-columns:repeat(auto-fill, minmax(5rem, 1fr))}

.following-user{margin-top:0.8rem;display:flex;justify-content:center;align-items:center;flex-direction:column}.following-user__photo{object-fit:cover;width:4rem;height:4rem;border-radius:50%}.following-user__name{font-size:0.8rem;font-weight:400;color:#343a40;margin-top:0.3rem}

